/* App.css */
.App {
  font-family: Arial, sans-serif;
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #4CAF50; /* Green background */
  color: white;
}

.app-header h1 {
  margin: 0;
}

.navbar {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.nav-link {
  text-decoration: none;
  color: white; /* White text */
  padding: 10px 20px;
  margin: 0 10px;
  font-weight: bold;
  background-color: #4CAF50; /* Green background */
  border: none;
  border-radius: 5px; /* Rounded corners */
  transition: background-color 0.3s ease;
}

.nav-link:hover {
  background-color: #45a049; /* Darker green on hover */
}

.auth-links {
  display: flex;
}

.auth-links .nav-link {
  margin-left: 10px;
}
